@mixin mymix {
	@media screen and #{$tablet} {
		flex-direction: column; } }

@mixin rainbow {
	@for $i from 1 through length($color-collection) {
		tr:nth-child(#{length($color-collection)}n+#{$i}) {
			background-color: desaturate(nth($color-collection,$i),50%); } } }



